<template>
  <div>
    <div
      class="dialog-mask-layer"
      v-if="visible && isShowMaskLayer"
      @click.self="touchMaskLayer"
      :style="getIndex"
    ></div>

    <div class="dialog-frame" v-if="visible" :style="getIndex">
      <!-- dialog -->
      <div class="dialog-frame-header">
        <div>
          <!--  -->
          <slot name="title"></slot>
        </div>
        <div @click="cancelDialog">X</div>
      </div>

      <div class="dialog-frame-body">
        <!--  -->
        <slot name="content"></slot>
      </div>

      <div class="dialog-frame-footer">
        <!--  -->
        <slot name="footer" :changeDialog="changeDialog">
          <yulang-button @click="cancelDialog"></yulang-button>
          <yulang-button @click="confirmCommit"></yulang-button>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
import YulangButton from '../yulang-button/YulangButton.vue';
export default {
  components: { YulangButton },
  name: 'yulang-dialog',
  data() {
    return {};
  },
  props: {
    // 
    visible: {
      type: Boolean,
      default: false,
    },
    // 
    beforeClose: {
      type: Function,
      default: undefined,
    },
    // 
    isShowMaskLayer: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    // index
    getIndex() {
      return {
        zIndex: this.$yulangIndex.getIndex(),
      };
    },
  },
  methods: {
    // 
    changeDialog(value) {
      this.$emit('update:visible', value);
    },
    // 
    openDialog() {
      this.$emit('update:visible', !this.visible);
    },
    // 
    cancelDialog() {
      this.$emit('update:visible', !this.visible);
    },
    // 
    touchMaskLayer() {
      // ,
      this.beforeClose
        ? this.beforeClose(this.cancelDialog)
        : this.cancelDialog();
    },
    // 
    confirmCommit() {
      this.$emit('update:visible', !this.visible);
    },
  },
};
</script>

<style lang="less" scoped>
@import url(./index.less);
</style>
