export default {
  inserted(el, binding) {
    el.addEventListener("mousedown", () => {
      const timeStart = new Date().getTime(); //
      binding.time = setInterval(function () //setInterval100
      {
        const timeEnd = new Date().getTime(); //100
        //300
        if (timeEnd - timeStart > 300) {
          clearInterval(binding.time); // clearInterval
          binding.value("down"); // callback
        }
      }, 100);
    });

    el.addEventListener("mouseup", () => {
      clearInterval(binding.time);
      binding.value("up");
    });
  },
};
