export const clearObj = {
  props: {
    // 
    clearable: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    // icon
    guanbiIconIsShow() {
      return this.valueComputed && !this.disabled && this.clearable;
    },
  },
  methods: {
    // icon
    clearValue() {
      this.valueComputed = null;
    },
  },
};
