<template>
  <div>
    <!--  -->
    <div @click="clickBtn" ref="upload">
      <slot></slot>
    </div>

    <!--  -->
    <div class="TipClass">
      <slot name="tips"></slot>
    </div>

    <!--  -->
    <slot
      name="fileListSlot"
      :fileListSuccess="fileListSuccess"
      :fileListDelete="deleteItem"
    >
      <transition-group name="fileItemAnimation">
        <div
          v-for="item in fileListSuccess"
          :key="item.id"
          class="fileListClass"
        >
          <!-- .txt,mdif -->
          <img :src="item.url" alt="" class="fileItemImageClass" />
          {{ item.name }}
          <img src="./assets/delete.svg" alt="" @click="deleteItem(item)" />
        </div>
      </transition-group>
    </slot>
  </div>
</template>

<script>
export default {
  name: 'yulang-upload',
  model: {
    prop: 'fileList',
    event: 'input',
  },
  data() {
    return {
      fileListCopy: this.fileList,
    };
  },
  props: {
    // 
    action: {
      type: String,
      default: undefined,
    },
    // 
    fileList: {
      type: Array,
      default: () => [],
    },
    // 
    limit: {
      type: Number,
      default: 3,
    },
    // 
    onPreview: {
      type: Function,
      default: undefined,
    },
    // kbmb
    singlePicExceed: {
      type: String,
      validator: (val) => {
        const onePattern = /^([0]|[1-9][0-9]*)[k][b]$/;
        const twoPattern = /^([0]|[1-9][0-9]*)[m][b]$/;
        return onePattern.test(val) || twoPattern.test(val);
      },
    },
  },
computed: {
  // 
  fileListSuccess: {
    get() {
      return this.fileList;
    },
    set(value) {
      this.$emit('input', value);
    },
  },
},
  methods: {
    clickBtn() {
      // 
      if (this.fileListSuccess.length >= this.limit) {
        this.onPreview(this.limit, this.fileListSuccess);
      } else {
        const input = document.createElement('input');
        input.type = 'file';
        input.click();
        input.addEventListener('change', (e) => {
          // 
          if (e.target.files[0].type.includes('image')) {
            // byte
            let maxSize;
            if (this.singlePicExceed.includes('kb')) {
              maxSize = this.singlePicExceed.split('kb')[0] * 1024;
            } else if (this.singlePicExceed.includes('mb')) {
              maxSize = this.singlePicExceed.split('mb')[0] * 1024 * 1024;
            }
            // 
            if (maxSize < e.target.files[0].size) {
              alert('');
            } else {
              this.$listeners.uploadSuccessCallback &&
                this.$listeners.uploadSuccessCallback(e.target.files[0]);

              // 
              if (!this.$listeners.uploadSuccessCallback) {
                // 
                if (this.action) {
                  let url = URL.createObjectURL(e.target.files[0]);
                  var image = new Image();
                  image.src = url;

                  image.onload = () => {
                    let BlobData = this.getBlobImage(image);
                    // base64
                    this.sendXML(BlobData).then((res) => {
                      console.log(res);
                    });
                  };
                } else {
                  // ,action
                  let obj = {};
                  let url = URL.createObjectURL(e.target.files[0]);
                  let image = new Image();
                  image.src = url;
                  obj.url = url;
                  obj.id = new Date().getTime();
                  obj.name = e.target.files[0].name;
                  this.fileListSuccess.push(obj)
                }
              }
            }
          }
        });
      }
    },
    // 
    deleteItem(row) {
      this.fileListSuccess = this.fileListSuccess.filter(
        (item) => item.id != row.id
      );
    },
    getBlobImage(img) {
      var canvas = document.createElement('canvas');
      var width = img.naturalWidth || img.width;
      var height = img.naturalHeight || img.height;
      canvas.width = width;
      canvas.height = height;
      var ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0, width, height); // 
      var ext = img.src.substring(img.src.lastIndexOf('.') + 1).toLowerCase(); // 
      var dataURL = canvas.toDataURL('image/' + ext); //  data URI

      // base64
      // function dataURLtoBlob(dataurl) {
      //   var arr = dataurl.split(','),
      //     mime = arr[0].match(/:(.*?);/)[1],
      //     bstr = atob(arr[1]),
      //     n = bstr.length,
      //     u8arr = new Uint8Array(n);
      //   while (n--) {
      //     u8arr[n] = bstr.charCodeAt(n);
      //   }
      //   return new Blob([u8arr], {
      //     type: mime,
      //   });
      // }

      return dataURL;
    },
    sendXML(imgbase64) {
      return new Promise((resolve) => {
        // xml
        const xhr = new XMLHttpRequest();
        // ()
        xhr.open('POST', this.action);
        // 
        xhr.send(imgbase64);

        xhr.onreadystatechange = function () {
          // 4
          console.log(xhr.readyState);
          if (xhr.readyState === 4) {
            // 200300
            if (xhr.status >= 200 && xhr.status < 300) {
              resolve(xhr);
            }
          }
        };
      });
    },
  },
  mounted() {},
};
</script>

<style lang="less" scoped>
@import url(./index.less);
</style>
