<template>
  <div
    :class="{
      'packages-yulang-table-container': true,
      'packages-yulang-table-container__border': isShowBorder,
    }"
    :style="{ '--width--': width }"
    ref="tableContainer"
  >
    <div class="hidden-columns">
      <slot></slot>
    </div>
    <!--  -->
    <div
      class="yulang-table__header-wrapper"
      :class="isShowBorder ? 'yulang-table__header-border' : ''"
      ref="titleTableRef"
    >
      <table :width="computedTable" border="0" cellpadding="0" cellspacing="0">
        <thead>
          <tr class="yulang-table__title__tbody">
            <td
              v-for="(item, index) in fieldSort"
              :key="index"
              :width="bisectRemainWidth"
              :style="[getWidthInfo(item.width), computedFixedPosition(index)]"
              :class="[
                'yulang-table__cell',
                'yulang-table__cell__title',
                isShowBorder ? 'yulang-table__cell__border' : '',
                item.width ? 'table-cell-width' : '',
              ]"
            >
              <div v-if="item.type === 'radio'"></div>
              <yulang-checkbox
                v-else-if="item.type === 'checkbox'"
                @change="headCheckChange"
                v-model="headCheckbox"
              ></yulang-checkbox>
              <div v-else>{{ item.label }}</div>
            </td>
          </tr>
        </thead>
      </table>
    </div>
    <!--  -->
    <div
      :class="
        isShowFooter
          ? 'yulang-table__body-wrapper-hidden-scroll'
          : 'yulang-table__body-wrapper-show-scroll'
      "
      :style="{ '--data-height--': dataHeight }"
      ref="dataTableRef"
    >
      <table :width="computedTable" border="0" cellpadding="0" cellspacing="0">
        <tbody class="yulang-table__data__tbody">
          <tr
            v-for="(item, index1) in tableData.length"
            :key="index1"
            class="yulang-table__data__tr"
          >
            <td
              v-for="(item2, index2) in fieldSort"
              :key="index2"
              :width="bisectRemainWidth"
              :style="[
                getWidthInfo(item2.width),
                computedFixedPosition(index2),
              ]"
              :class="[
                'yulang-table__cell',
                index2 === 0 && isShowBorder ? 'yulang-table__cell__first' : '',
                isShowBorder ? 'yulang-table__cell__border' : '',
                item2.width ? 'table-cell-width' : '',
              ]"
            >
              <!-- @click="tbodyTdClick(tableData[item - 1])" -->
              <!-- <div v-if="item2.el" v-YulangAddDom="item2.el"></div> -->
              <yulang-radio
                v-if="item2.type === 'radio'"
                :label="item2.index2"
                v-model="tableData[index1]._yulangIsRadio"
              ></yulang-radio>
              <yulang-checkbox
                v-else-if="item2.type === 'checkbox'"
                v-model="tableData[index1]._yulangIsCheckbox"
              ></yulang-checkbox>
              <div v-else>
                <RenderDom
                  v-if="item2.vNode"
                  :vNode="item2.vNode"
                  :el="item2.el"
                  :rowData="tableData[index1]"
                ></RenderDom>
                <div v-else>
                  {{ tableData[index1] ? tableData[index1][item2.prop] : "" }}
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!--  -->
    <div class="yulang-table__footer-wrapper" ref="footerTableRef">
      <table :width="computedTable" border="0" cellpadding="0" cellspacing="0">
        <tfoot>
          <tr
            v-for="(item, index1) in footerMethod(data, fieldSort)"
            :key="index1"
            class="yulang-table__footer__tbody"
          >
            <td
              v-for="(item2, index2) in fieldSort"
              :key="index2"
              :width="bisectRemainWidth"
              :style="[
                getWidthInfo(item2.width),
                computedFixedPosition(index2),
              ]"
              :class="[
                'yulang-table__cell',
                index2 === 0 && isShowBorder ? 'yulang-table__cell__first' : '',
                isShowBorder ? 'yulang-table__cell__border' : '',
                item2.width ? 'table-cell-width' : '',
              ]"
            >
              {{ item[index2] }}
            </td>
          </tr>
        </tfoot>
      </table>
    </div>
  </div>
</template>

<script>
import YulangRadio from "@/packages/yulang-radio/YulangRadio.vue";
import YulangCheckbox from "@/packages/yulang-checkbox/YulangCheckbox.vue";

export default {
  name: "yulang-table",
  components: {
    YulangRadio,
    YulangCheckbox,
    RenderDom: {
      props: ["el", "vNode", "rowData"],
      render() {
        return this.vNode;
      },
    },
  },
  props: {
    data: {
      type: Array,
    },
    // table
    width: {
      type: String,
      default() {
        return "100%";
      },
    },
    // table,
    height: {
      type: String,
      default() {
        return "auto";
      },
    },
    // item
    minWidth: {
      default() {
        return "100px";
      },
    },
    // 
    isShowBorder: {
      default() {
        return false;
      },
    },
    // 
    isShowFooter: {
      type: Boolean,
      default() {
        return false;
      },
    },
    // 
    footerMethod: {
      type: Function,
      default() {
        return [];
      },
    },
    // 
    footerTitleWidth: {
      default: "50px",
    },
  },
  data() {
    return {
      // data
      tableData: [],
      fieldSort: [],
      // itemrightpx
      fixedRightPosition: [],
      fixedRightStratIndex: 0,
      // itemleftpx
      fixedLeftPosition: [],
      fixedLeftStratIndex: 0,
      // 
      isListenScroll: false,
      // 
      dataHeight: 0,
      // 
      headCheckbox: false,
    };
  },
  computed: {
    // 
    bisectRemainWidth() {
      // item
      let noSetWidth = 0;
      // width
      const sum = this.fieldSort.reduce((pre, item) => {
        if (item.wicomputedTabledth) {
          return pre + parseFloat(item.width.split("px")[0]);
        }
        noSetWidth++;
        return pre;
      }, 0);
      const tableWidth = this.$refs.tableContainer.clientWidth;
      // 
      const borderWidth = this.isShowBorder ? this.fieldSort.length : 0;
      const computedWidth = (tableWidth - sum - borderWidth) / noSetWidth;
      // 
      return computedWidth > this.minWidth.split("px")[0]
        ? computedWidth - 10 + "px"
        : this.minWidth;
    },
    // table100%
    computedTable() {
      // tablewidthwidthminwidth
      const allWidth = this.fieldSort.reduce((pre, item) => {
        if (item.width) {
          return pre + parseFloat(item.width.split("px")[0]);
        } else {
          return pre + parseFloat(this.minWidth.split("px")[0]);
        }
      }, 0);
      const tableWidth = this.$refs?.tableContainer?.clientWidth;
      return allWidth > tableWidth ? allWidth + "px" : "auto";
    },
    // fixed
    computedFixedPosition() {
      return function (index) {
        if (this.fieldSort[index].fixed === "left") {
          return {
            position: "sticky",
            left: this.fixedLeftPosition[index] + "px",
            backgroundColor: "inherit",
          };
        }
        if (this.fieldSort[index].fixed === "right") {
          return {
            position: "sticky",
            right: this.fixedRightPosition[index] + "px",
            backgroundColor: "inherit",
          };
        }
      };
    },
  },
  methods: {
    // width
    getWidthInfo(width) {
      return {
        "--cell-width--": width,
        "--cell-min-width--": this.minWidth,
      };
    },
    // fixed
    setFixedWidth() {
      // 
      let rightDistance = 0; // 
      let fixedRightStratIndex = this.fieldSort.length;
      for (let i = this.fieldSort.length - 1; i >= 0; i--) {
        if (this.fieldSort[i].fixed === "right") {
          fixedRightStratIndex = i;
          this.fixedRightPosition.unshift(rightDistance);
          rightDistance =
            rightDistance + this.fieldSort[i].width
              ? parseFloat(this.fieldSort[i].width.split("px")[0])
              : this.minWidth;
        } else {
          this.fixedRightPosition.unshift(rightDistance);
        }
      }
      // 
      this.fixedRightStratIndex = fixedRightStratIndex;

      // 
      let leftDistance = 0; // 
      let fixedLeftStratIndex = -1;
      for (let i = 0; i < this.fieldSort.length; i++) {
        if (this.fieldSort[i].fixed === "left") {
          fixedLeftStratIndex = i;
          this.fixedLeftPosition.push(leftDistance);
          leftDistance =
            leftDistance + this.fieldSort[i].width
              ? parseFloat(this.fieldSort[i].width.split("px")[0])
              : this.minWidth;
        } else {
          this.fixedLeftPosition.push(leftDistance);
        }
      }
      // 
      this.fixedLeftStratIndex = fixedLeftStratIndex;
    },
    // data
    setScrollListen() {
      this.$refs.dataTableRef.addEventListener("scroll", () => {
        this.$refs.titleTableRef.scrollLeft =
          this.$refs.dataTableRef.scrollLeft;
      });
      this.$refs.footerTableRef.addEventListener("scroll", () => {
        this.$refs.titleTableRef.scrollLeft =
          this.$refs.footerTableRef.scrollLeft;
        this.$refs.dataTableRef.scrollLeft =
          this.$refs.footerTableRef.scrollLeft;
      });
    },
    //data
    setDataHeight() {
      if (this.height === "auto") {
        this.dataHeight = "auto";
      } else {
        this.dataHeight =
          parseFloat(this.height.split("px")[0]) -
          this.$refs?.titleTableRef?.clientHeight +
          "px";
      }
    },
    // table
    initTableData() {
      this.tableData = this.data.map((item) => {
        item._yulangIsRadio = false;
        item._yulangIsCheckbox = false;
        return item;
      });
    },
    // dom
    initDom() {
      this.$children.forEach((item) => {
        if (item.yulangComponentName === "yulang-table-item") {
          /*
           * prop:itemkey
           * width:item
           * fixed: 'left''right'
           * vNode: 
           * type: 'radio''checkbox''footer'item
           */
          if (item.$vnode.child.$el.childNodes.length > 0) {
            if (item.type === "radio" || item.type === "checkbox") {
              this.fieldSort.unshift({
                prop: item.prop,
                width: item.width,
                label: item.label,
                fixed: item.fixed,
                vNode: item.$vnode,
                type: item.type,
              });
            } else {
              this.fieldSort.push({
                prop: item.prop,
                width: item.width,
                label: item.label,
                fixed: item.fixed,
                vNode: item.$vnode,
              });
            }
          } else {
            if (item.type === "radio" || item.type === "checkbox") {
              this.fieldSort.unshift({
                prop: item.prop,
                width: item.width,
                label: item.label,
                fixed: item.fixed,
                type: item.type,
              });
            } else {
              this.fieldSort.push({
                prop: item.prop,
                width: item.width,
                label: item.label,
                fixed: item.fixed,
              });
            }
          }
        }
      });
      if (this.isShowFooter) {
        this.fieldSort.unshift({
          type: "footer",
          fixed: "left",
          width: this.footerTitleWidth,
        });
      }
    },
    // 
    getRadioData() {
      return this.tableData.find((item) => item._yulangIsRadio);
    },
    // 
    getCheckboxData() {
      return this.tableData.filter((item) => item._yulangIsCheckbox);
    },
    // 
    headCheckChange(value) {
      console.log(123);
      this.tableData = this.tableData.map((item) => {
        item._yulangIsCheckbox = value;
        return item;
      });
    },
  },
  mounted() {
    this.initDom();
    this.setFixedWidth();
    this.setScrollListen();
    this.setDataHeight();
    this.initTableData();
  },
};
</script>

<style lang="less" scoped>
@import url("./index.less");
</style>
