/* 
Node.appendChild() 
 appendChild() 

 Node.cloneNode() 
 cloneNode 
*/
export default {
  componentUpdated(el, binding) {
    // dom
    // dom
    let child = el.lastElementChild;
    while (child) {
      el.removeChild(child);
      child = el.lastElementChild;
    }
    el.appendChild(binding.value.cloneNode(true));
  },
};
