export const codeStrB = `
<template>
  <yulang-tabs v-model="activeName" @tabClick="handleClick">
    <yulang-tab-pane label="" name="first"></yulang-tab-pane>
    <yulang-tab-pane label="" name="second"></yulang-tab-pane>
    <yulang-tab-pane label="" name="three"></yulang-tab-pane>
    <yulang-tab-pane label="" name="four"
      ></yulang-tab-pane
    >
  </yulang-tabs>
</template>

<script>
export default {
  data() {
    return {
      activeName: "second",
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
  },
};
</script>

<style lang="less" scoped></style>
`

