router.beforeEach((to, from, next) => {
  // ,isAuthfalse
  if (!to.meta.isAuth) {
    // ,,...
    const auth = false;
    if (auth) {
      next();
    } else {
      alert("");
    }
  } else {
    next();
  }
});

router.afterEach((to) => {
  document.title = to.meta.title || "";
});

export default router;
