// 
export const passwordObj = {
  props: {
    // 
    showPassword: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isShowPassword: false,
    };
  },
  methods: {
    // 
    passwordEyeClick(type) {
      this.isShowPassword = type === "down";
    },
  },
};
