// 
export const styleObj = {
  props: {
    // 
    placeholder: {
      default: "",
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    suffixIcon: {
      type: String,
    },
    // 
    prefixIcon: {
      type: String,
    },
    // 
    rows: {
      type: Number,
      default: 1,
    },
    // 
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    // inputclass
    inputClassComputed() {
      return {
        " package-yulang-input": true,
        "input-disabled": this.disabled,
      };
    },
  },
};
