export const codeStrB = `
<template>
  <div>
    <yulang-popover
      title=""
      :minWidth="100"
      trigger="hover"
      content=",,,"
      placement="right-end"
    >
      <template #reference>
        <yulang-button>hover </yulang-button>
      </template>
    </yulang-popover>
    <yulang-popover
      title=""
      :minWidth="100"
      trigger="click"
      content=",,,"
    >
      <template #reference>
        <yulang-button>click </yulang-button>
      </template>
    </yulang-popover>
    <yulang-popover
      title=""
      :minWidth="100"
      trigger="focus"
      content=",,,"
    >
      <template #reference>
        <yulang-button>focus </yulang-button>
      </template>
    </yulang-popover>
    <yulang-popover
      ref="popover4"
      title=""
      :minWidth="100"
      content=",,,"
    >
      <template #reference>
        <yulang-button @click="btnClick"></yulang-button>
      </template>
    </yulang-popover>
  </div>
</template>

<script>
export default {
  methods: {
    btnClick() {
      this.$refs.popover4.showChange();
    },
  },
};
</script>

<style lang="less" scoped></style>
`

